export default {
	data() {
		return {
			title: 'onAccelerometerChange',
			value: ''
		}
	},
	onUnload() {
		uni.stopAccelerometer();
	},
	methods: {







		watchAcce() {
			uni.onAccelerometerChange((res) => {
				this.value = "监听设备的加速度变化:\n" + "X轴：" + res.x.toFixed(2) + "\nY轴：" + res.y.toFixed(2) +
					"\nZ轴：" + res.z.toFixed(2);
			})
		},
		stopAcce() {
			uni.stopAccelerometer()
		}
	}
}
